import { useEffect, useState } from 'react'

import { useParams } from 'react-router-dom'

import { getLog, retrieveCriticalActions, retrieveMetaData } from '../scripts/connect-firebase'

import Debriefing from '../components/Debriefing'
// import { checkPrompts } from '@meddbriefer/scenario-data/traumaScenarioV2'

const Log = () => {

  const { logID } = useParams()
  const [ log, setLog ] = useState(null)
  const [ metaData, setMetaData ] = useState(null)

  const [ criticalActions, setCriticalActions ] = useState(null)
  const [loaded, setLoaded] = useState(false)

  useEffect(
    () => {
      Promise.all([
        getLog(logID),
        retrieveMetaData("trauma", "V1"),
      ])
      .then(results => {
        setLog(results[0])
        const md = results[1].CHECKLIST_META_DATA

        setMetaData(md.concat(results[1].INTV_META_DATA))
      })
    }, [logID]
  )

  useEffect(
    () => {
      if (!!log && !criticalActions) {
        retrieveCriticalActions(log.scenarioName, "V1")
        .then(res => {
          setCriticalActions(res.criticalActions)
        })
      }
    }, [log, criticalActions]
  )

  useEffect(
    () => {
      if (!!criticalActions) {
        setLoaded(true)
      }
    }, [criticalActions]
  )

  if (!loaded) {
    return (<h3>Loading</h3>)
  }
  return ( <Debriefing log={log} metaData={metaData} criticalActions={criticalActions} /> )


}

export default Log;
